import React, { useState, useEffect } from 'react';
import { Route, Routes } from 'react-router-dom';
import { useForm, ValidationError } from '@formspree/react';

// Main App Component
const App = () => {
    const [currentPage, setCurrentPage] = useState('home');

    // Function to navigate between pages
    const navigateTo = (page) => {
        setCurrentPage(page);
    };

    return (
        <div className="min-h-screen bg-gray-100 font-sans antialiased flex flex-col">
            {/* // Header */}
            <Header navigateTo={navigateTo} currentPage={currentPage} />

            {/* // Main Content Area */}
            <main className="flex-grow container mx-auto px-4 py-8">
                {/* //Render the current page based on state */}
                {(() => {
                    switch (currentPage) {
                        case 'home':
                            return <HomePage />;
                        case 'services':
                            return <ServicesPage />;
                        case 'contact':
                            return <ContactPage />;
                        case 'about':
                            return <AboutPage />;
                        default:
                            return <HomePage />;
                    }
                })()}
            </main>

            {/* // Footer */}
            <Footer />
        </div>
    );
};

// Header Component
const Header = ({ navigateTo, currentPage }) => {
    const [isOpen, setIsOpen] = useState(false); // State for mobile menu

    const navItems = [
        { name: 'Home', id: 'home' },
        { name: 'Services', id: 'services' },
        { name: 'Contact', id: 'contact' },
        { name: 'About Us', id: 'about' },
    ];

    return (
        <header className="bg-gradient-to-r from-blue-700 to-blue-900 text-white shadow-lg">
            <div className="container mx-auto px-4 py-4 flex justify-between items-center">
                {/* // Logo/Site Title */} 
                <div className="flex items-center space-x-4">
                    <img src={logo} className="h-12 w-auto object-scale-down" alt="logo" />
                    <h1 className="text-3xl font-bold tracking-tight">
                        <a href="#" onClick={() => navigateTo('home')} className="hover:text-blue-200 transition-colors duration-300">
                            MDRI Global
                        </a>
                    </h1>
                </div>
                {/* // Mobile Menu Button */} 
                <button
                    className="md:hidden text-white focus:outline-none"
                    onClick={() => setIsOpen(!isOpen)}
                    aria-label="Toggle navigation"
                >
                    <svg className="w-8 h-8" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                        {isOpen ? (
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12"></path>
                        ) : (
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 6h16M4 12h16M4 18h16"></path>
                        )}
                    </svg>
                </button>

               {/*  // Desktop Navigation */} 
                <nav className="hidden md:flex space-x-6">
                    {navItems.map((item) => (
                        <a
                            key={item.id}
                            href="#"
                            onClick={() => navigateTo(item.id)}
                            className={`text-lg font-medium px-3 py-2 rounded-md transition-all duration-300
                                ${currentPage === item.id ? 'bg-blue-800 text-white' : 'hover:bg-blue-800 hover:text-white'}`}
                        >
                            {item.name}
                        </a>
                    ))}
                </nav>
            </div>

            {/* // Mobile Navigation Menu  */}
            {isOpen && (
                <nav className="md:hidden bg-blue-800 pb-4">
                    <ul className="flex flex-col items-center space-y-2">
                        {navItems.map((item) => (
                            <li key={item.id}>
                                <a
                                    href="#"
                                    onClick={() => { navigateTo(item.id); setIsOpen(false); }}
                                    className={`block w-full text-center py-2 px-4 rounded-md text-lg transition-all duration-300
                                        ${currentPage === item.id ? 'bg-blue-700 text-white' : 'hover:bg-blue-700 hover:text-white'}`}
                                >
                                    {item.name}
                                </a>
                            </li>
                        ))}
                    </ul>
                </nav>
            )}
        </header>
    );
};

