function ContactForm() {
  const [state, handleSubmit] = useForm("xgvzvpde");
  if (state.succeeded)
 {
      return <p>Thanks for your message!</p>;
  }
  return (
    <form onSubmit={handleSubmit}>
      <div>
            <label htmlFor="name" className="block text-gray-700 text-lg font-medium mb-2">Your Name</label>
            <input
                type="text"
                id="name"
                name="name"
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent transition duration-200"
                placeholder="John Doe"
                required
            />
        </div>
        <div>
            <label htmlFor="email" className="block text-gray-700 text-lg font-medium mb-2">Your Email</label>
            <input
                type="email"
                id="email"
                name="email"
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent transition duration-200"
                placeholder="john.doe@example.com"
                required
            />
             <ValidationError 
                prefix="Email" 
                field="email"
                errors={state.errors}
            />
        </div>
        <div>
            <label htmlFor="subject" className="block text-gray-700 text-lg font-medium mb-2">Subject</label>
            <input
                type="text"
                id="subject"
                name="subject"
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent transition duration-200"
                placeholder="Inquiry about services"
                required
            />
        </div>
        <div>
            <label htmlFor="message" className="block text-gray-700 text-lg font-medium mb-2">Your Message</label>
            <textarea
                id="message"
                name="message"
                rows="5"
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent transition duration-200 resize-y"
                placeholder="Type your message here..."
                required
            ></textarea>
               <ValidationError 
                    prefix="Message" 
                    field="message"
                    errors={state.errors}
                />
        </div>
        <button
            type="submit"
            className="w-full bg-blue-600 hover:bg-blue-700 text-white font-bold py-3 px-6 rounded-lg text-xl shadow-md transform transition-all duration-300 hover:scale-105"
        >
            Send Message
        </button>
      
    </form>
  );
}

